@import '../../styles/constants.scss';
.modal {
	position: absolute;
	height: 100vh;
	width: 100vh;
	top: 0;
	left: 0;
	z-index: 9998;
	&-backdrop {
		height: 100vh;
		width: 100vw;
		background: rgba(0, 0, 0, .5);
		position: relative;
		overflow: hidden;
		.show {
			display: inherit;
		}
	}
	&-container {
		color: black;
		z-index: 9999;
		position: absolute;
		background: #fff;
		top: 50%;
		left: 50%;
		display: flex;
		flex-direction: column;
		padding: 1em;
		display: none;
		// margins are set in js file
	}
}
